<template>
  <div class="test">
    <span
      class="icon icon-minus mr-2"
      :class="{'icon-disabled' : value == 1}"
      @click="setScore(value - 1)"
    />
    <div class="skill_levels">
      <template v-for="i in 4">
        <div
          @mouseover="setHover(i)"
          @mouseleave="resetHover"
          @click="setScore(i)"
          :class="['level level_' + i, (hoverItem >= i || value >= i) && ('level_' + i + '_active')]"
        />
      </template>
    </div>
    <span
      class="icon icon-plus ml-2"
      :class="{'icon-disabled' : value == 4}"
      @click="setScore(value + 1)"
    />
  </div>
</template>

<script>
export default {
  name: "SkillScore",
  props: {
    value: Number,
  },
  data() {
    return {
      hoverItem: null,
    }
  },
  methods: {
    setHover(i) {
      this.hoverItem = i
    },
    resetHover() {
      this.hoverItem = null
    },
    setScore(i) {
      if (i >= 1 && i <= 4) {
        this.$emit('input', i)
      }
    }
  }
}
</script>

<style scoped>
.test {
  display: flex;
}

.skill_levels {
  display: flex;
  align-items: center;
  width: 144px;
}

.level {
  margin: 0 1px;
  background-color: #C4C4C4;
  width: 32%;
  height: 10px;
}

.level_1 {
  width: 4%;
}

.level_1:hover,
.level_1_active {
  background: #202257;
}

.level_2:hover,
.level_2_active {
  opacity: .8;
  background: #1EB295;
}

.level_3:hover,
.level_3_active {
  opacity: .9;
  background: #1EB295;
}

.level_4:hover,
.level_4_active {
  background: #1EB295;
}
</style>
